* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

ul {
    list-style: none;
}

.App {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-family: Arial, Helvetica, sans-serif;
    font-size: 15px;
    color: #000;
    max-width: 400px;
    text-align: center;
    background-color: #dfe6e9;
    padding: 30px;
    border-radius: 5px;
}

.App h2 {
    margin-bottom: 10px;
}

.todo-form {
    display: flex;
    justify-content: center;
    align-items: center;
}

.todo-form input {
    padding: 10px 10px;
    border-radius: 20px;
    border: 2px solid #e17055;
    outline: none;
}

.todo-form button {
    width: 40px;
    height: 40px;
    margin-left: 10px;
    font-size: 30px;
    text-align: center;
    line-height: 22px;
    border-radius: 50%;
    background-color: #e17055;
    color: #fff;
    border: none;
    outline: none;
    cursor: pointer;
    transition: all .3s;
}

.todo-form button:hover {
    opacity: .8;
}

.TodoList ul {
    min-width: 300px;
    max-height: 200px;
}

.Todo {
    margin-top: 30px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #fff;
    border-radius: 20px;
    box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.5);
}

.Todo li {
    flex: 1;
}

.Todo li.completed {
    text-decoration: line-through;
    color: gray;
}

.Todo .complete-btn {
    padding: 10px;
    border: none;
    background-color: #00b894;
    font-size: 16px;
    color: #fff;
    cursor: pointer;
    outline: none;
}

.Todo .delete-btn {
    padding: 10px;
    border: none;
    background-color: #d63031;
    font-size: 16px;
    color: #fff;
    cursor: pointer;
    border-top-right-radius: 20px;
    border-bottom-right-radius: 20px;
    outline: none;
}